<style>
  /*不显示竖向Menu的边栏*/
  .ivu-menu-vertical.ivu-menu-light:after {
    width: 0px;
  }

  .leftPanel {
    height: 100%;
  }

  .rightPanel {
    display: flex;
    display: -webkit-flex;
    -webkit-box-orient: vertical;
    -webkit-flex-flow: column;
    flex-flow: column;
    /*padding: 10px;*/
  }
</style>
<template>
  <Row class="fullWidth">
    <iCol span="1" class="leftPanel left-background">
      <Row style="padding: 7px;" class="left-top">
        <span style="font-size: 18px;">{{ currentOrgName }}</span>
      </Row>
      <Menu activeName="plan" width="auto" class="left-menu" :open-names="['maintenance']" @on-select="onNaviMenuClick">
        <Submenu name="maintenance">
          <template slot="title">
            <span>维保工作</span>
            <img src="../../assets/menu/up.png" class="menu-up" height="4" width="8"/>
            <img src="../../assets/menu/down.png" class="menu-down" height="4" width="8"/>
          </template>
          <!--<Menu-item name="my_task">我的任务</Menu-item>-->
          <!--<Menu-item name="maintain">故障维修</Menu-item>-->
          <Menu-item name="plan">维保计划</Menu-item>
          <Menu-item name="task">维保任务</Menu-item>
        </Submenu>
        <Submenu name="supervisory">
          <template slot="title">
            <span>监督检查</span>
            <img src="../../assets/menu/up.png" class="menu-up" height="4" width="8"/>
            <img src="../../assets/menu/down.png" class="menu-down" height="4" width="8"/>
          </template>
          <Menu-item name="record">检查记录</Menu-item>
          <Menu-item name="order_rect">责令整改</Menu-item>
        </Submenu>
        <!--<Submenu name="self">-->
          <!--<template slot="title">-->
            <!--<span>自我管理</span>-->
            <!--<img src="../../assets/menu/up.png" class="menu-up" height="4" width="8"/>-->
            <!--<img src="../../assets/menu/down.png" class="menu-down" height="4" width="8"/>-->
          <!--</template>-->
          <!--<Menu-item name="correction">自查自纠</Menu-item>-->
          <!--<Menu-item name="rectification">隐患整改</Menu-item>-->
          <!--<Menu-item name="drill">培训演练</Menu-item>-->
        <!--</Submenu>-->
      </Menu>
    </iCol>
    <iCol class="rightPanel">
      <router-view></router-view>
    </iCol>
  </Row>
</template>

<script>
  import config from '../../config/config'

  export default {
    data() {
      return {
        currentOrgName: localStorage.current_org_name,
      };
    },
    methods: {
      fetchData: function () {
        document.title = '单位管理 - ' + config.web_name;
        this.$store.commit('setActiveNaviMenu', 'routine');
      },
      backToWelcome: function () {
        this.$router.push("/welcome");
      },
      onNaviMenuClick: function (key) {
        //Route to sub module
        this.$router.push(key);
      }
    },
    mounted() {
      let self = this;
      self.fetchData();
    }

  }
</script>



